<template>
<!-- 用户管理组件 -->
  <div class="permissionUserTable">
    <div class="createButton">
      <el-button type="primary" @click="dialogFormVisible = true">创建新用户</el-button>
    </div>
    <!-- 用户信息表格 -->
    <div class="table">
      <el-table :data="tableData" stripe style="width: 100%">
        <el-table-column prop="nickName" label="用户名称" width="120px"></el-table-column>
        <el-table-column prop="userName" label="姓名" width="120px"></el-table-column>
        <el-table-column prop="department" label="所属部门" width="120px"></el-table-column>
        <el-table-column prop="work" label="工作单位" width="120px"></el-table-column>
        <el-table-column prop="activity" label="操作" width="520px">
          <template>
            <el-button class="changeBtn" type="primary">修改门类</el-button>
            <el-button class="changeBtn" type="primary" @click="dialogFormVisible2 = true">修改权限</el-button>
            <el-button class="changeBtn" type="primary" @click="dialogFormVisible1 = true">修改绑定角色</el-button>
            <el-button class="changeBtn" type="primary" >编辑用户</el-button>
            <span>
              <i class="iconfont icon-shanchu delete"></i>
            </span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 新建用户表单 -->
    <div class="createForm" :model="formInline">
      <el-dialog title="新建用户-用户名称" :visible.sync="dialogFormVisible" :modal="false">
        <div class="createFormMain">
        <el-form :model="form" :inline="true">
          <el-form-item label="用户代码" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="用户名" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="用户密码" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="审批权限" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="姓名" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="工作单位" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="部门" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="注册资质" :label-width="formLabelWidth">
            <el-select v-model="form.region" placeholder="请选择注册资质">
              <el-option label="10年" value="shanghai"></el-option>
              <el-option label="20年" value="beijing"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        </div>
        <div slot="footer" class="roleForm-footer">
        <el-button @click="btnResetClick">重 置</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">保 存</el-button>
        </div>
      </el-dialog>
    </div>
    <!-- 绑定用户操作 -->
    <div class="choseForm">
      <el-dialog title="绑定角色-用户名称" :visible.sync="dialogFormVisible1" :modal="false">
        <div class="choseFormMain">
          <el-checkbox-group v-model="checkedRoles" @change="handleCheckedCitiesChange">
            <el-checkbox v-for="role in roles" :label="role" :key="role" border>{{role}}</el-checkbox>
          </el-checkbox-group>
        </div>
        <div slot="footer" class="choseForm-footer">
          <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
          <el-button @click="btnResetClick">重 置</el-button>
          <el-button type="primary" @click="dialogFormVisible1 = false">保 存</el-button>
        </div>
      </el-dialog>
    </div>
    <!-- 新增权限表单 -->
    <div class="createPermission">
      <el-dialog title="新增权限-角色名称" :visible.sync="dialogFormVisible2" :modal="false">
        <div class="chooseTab">
          <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
          <el-tab-pane label="用户管理" name="first">
          <div class="permissionTree">
          <el-tree
             :props="props"
             :load="loadNode"
              lazy
              show-checkbox
              @check-change="handleCheckChange">
          </el-tree>
           </div>
           </el-tab-pane>
           <el-tab-pane label="配置管理" name="second">
           <div class="permissionTree" v-if="isShow">
           <el-tree
             :props="props"
             :load="loadNode"
              lazy
              show-checkbox
              @check-change="handleCheckChange">
             </el-tree>
            </div>
          </el-tab-pane>
           <el-tab-pane label="角色管理" name="third">角色管理</el-tab-pane>
           <el-tab-pane label="定时任务补偿" name="fourth">定时任务补偿</el-tab-pane>
          </el-tabs>
          </div>
          <div slot="footer" class="createPermission-footer">
          <el-button @click="btnResetClick">重 置</el-button>
          <el-button type="primary" @click="dialogFormVisible2 = false">保 存</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
const roleOptions = ['客户', '经理', '客户', '经理']
export default {
  name: 'permissionUserTable',
  data () {
    return {
      show: '',
      isShow: true,
      dialogFormVisible: false,
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      checkAll: false,
      roles: roleOptions,
      checkedRoles: [],
      activeName: 'first',
      checkboxGroup1: [],
      checkboxGroup2: [],
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      formLabelWidth: '70px',
      dynamicTags: ['用户一', '用户二', '用户三'],
      inputVisible: false,
      inputValue: '',
      tableData: [
        {
          discription: '2016-05-02',
          nickName: '王小虎',
          userName: '王小虎',
          department: '1',
          work: '办公室'
        },
        {
          discription: '2016-05-04',
          nickName: '王小虎',
          userName: '王小虎',
          department: '1',
          work: '办公室'
        }
      ],
      props: {
        label: 'name',
        children: 'zones'
      }
    }
  },
  mounted () {
    this.getTitle()
  },
  methods: {
    // 全选的方法
    handleCheckAllChange (val) {
      this.checkedRoles = val ? roleOptions : []
      this.isIndeterminate = false
    },
    handleCheckedCitiesChange (value) {
      let checkedCount = value.length
      this.checkAll = checkedCount === this.roles.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.roles.length
    },
    handleClick (tab, event) {
      console.log(tab, event)
    },
    // 与树有关的方法
    handleCheckChange (data, checked, indeterminate) {
      console.log(data, checked, indeterminate)
    },
    handleNodeClick (data) {
      console.log(data)
    },
    loadNode (node, resolve) {
      if (node.level === 0) {
        return resolve([{ name: 'region1' }, { name: 'region2' }])
      }
      if (node.level > 3) return resolve([])
      var hasChild
      if (node.data.name === 'region1') {
        hasChild = true
      } else if (node.data.name === 'region2') {
        hasChild = false
      } else {
        hasChild = Math.random() > 0.5
      }
      setTimeout(() => {
        var data
        if (hasChild) {
          data = [{
            name: 'zone' + this.count++
          }, {
            name: 'zone' + this.count++
          }]
        } else {
          data = []
        }
        resolve(data)
      }, 500)
    }
  }

}
</script>
<style>
.delete {
  color: #ed5b23 !important;
  margin-left: 2%;
}
.table {
  width: 100%;
  height: 100%;
  margin-left: 16%;
}
.createButton {
  width: 100%;
  height: 100%;
  margin-left: 80%;
  margin-top: 2%;
}
.createFormMain {
  margin-left:4%;
}
.choseFormMain{
  width:400px;
  height: 300px;
  margin-left: 4%;
}
.el-checkbox.is-bordered{
  width: 135px;
}
.el-checkbox{
  margin-top: 15px;
  margin-left: 10px;
}
.createPermission-footer.choseForm-footer {
  margin-right: 40%;
}
.changeBtn:focus {
  background-color: #f69944;
  border-color: #f69944;
  color: #fff;
}
/* .swiper-wrapper{
  margin-left: 5%;
  max-width: 500px;
  overflow: auto;
} */
.createForm .el-dialog__body{
  max-height: 300px;
  overflow: auto;
}
.roleBtn{
  margin-left: 7px;
}
.permissionTree{
  max-height: 300px;
  overflow: auto;
  margin-top: 4%;
  height: 250px;
  margin-left: 50px;
}
.roleForm-footer{
  margin-right:40% ;
}
.el-tabs__nav-scroll {
  max-width: 390px;
  width: 390px;
  overflow: auto;
  overflow-x: hidden;
}
</style>



