<template>
  <div class="permissionPage">
    <div class="mainpage_header">
      <div class="header_border">
        <span class="header_logo">
          <img src="@/assets/images/jylogo.png">
        </span>
        <div class="header_title">
          <!-- <img src="@/assets/images/logo01.png"/> -->
          欢迎使用今易档案管理系统
        </div>
        <div class="header_icon">
          <span style="margin-left: 0px; cursor: pointer;">
            <i class="iconfont icon-fanhui back" @click="backToHome"></i>
          </span>
          <span style="margin-left: 0px; cursor: pointer;" class="exitIcon" @click.stop="logOut">
            <i class="iconfont icon-dingbudaohang-zhangh exit"></i>
          </span>
        </div>
      </div>
    </div>
    <div class="permissionMain">
      <div class="checkButton">
        <el-button-group>
          <el-button class="manageBtn" @click.stop="changeTable('permissionRoleTable')">角色管理</el-button>
          <el-button class="manageBtn" @click.stop="changeTable('permissionUserTable')">用户管理</el-button>
        </el-button-group>
      </div>
      <div :is="currentView"></div>
    </div>
  </div>
</template>

<script>
import permissionRoleTable from '@/components/perssionPage/PermissionRoleTable.vue'
import permissionUserTable from '@/components/perssionPage/PermissionUserTable.vue'
export default {
  data () {
    return {
      permissionTable1: 'permissionRoleTable',
      permissionTable2: 'permissionUserTable',
      currentView: 'permissionRoleTable'
    }
  },
  components: {
    permissionRoleTable,
    permissionUserTable
  },
  methods: {
    changeTable: function (tableItem) {
      this.currentView = tableItem
    },
    backToHome: function () {
      this.$router.replace({
        name: 'archivelist'
      })
    },
    logOut: function () {
      this.$confirm('您确定退出登录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // console.log('1111111')
          let that = this
          that.$axios
            .post(that.api + '/Logout' + that.actionExt)
            .then(function (res) {
              // console.log('跳入验证', res)
              if (res.data.code === 0) {
                that.$router.replace('/login')
              } else {
                that.$message.error(res.data.errMsg)
              }
            })
            .catch(function (error) {
              console.log(error)
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    }
  }
}
</script>

<style>
/* @media screen and (max-width: 668px) {
  .button div {
    width: 28px;
    height: 28px;
  }
} */
.permissionPage {
  height: 100%;
  width: 100%;
  background-image: url('/static/assets/images/baseImg.jpg');
  background-size: cover;
}
.mainpage_header {
  background-image: url('/static/assets/images/top-pic.jpg');
  background-size: 100% 100%;
  height: 60px;
}
.header_border {
  width: 100%;
  height: 100%;
  position: relative;
}
.header_logo {
  display: block;
  position: absolute;
  height: 100%;
  left: 8px;
  /*top: 24%; */
}
.header_logo img {
  width: auto;
  height: inherit;
}
.header_title {
  height: 100%;
  line-height: 60px;
  color: #fff;
  margin-left: 9%;
  font-size: 12px;
  width: 202px;
  text-align: center;
}
.header_icon {
  color: #fff;
  position: absolute;
  right: 122px;
  top: 0;
  height: 60px;
  line-height: 60px;
}
.exit {
  color: #fff !important;
  font-size: 25px !important;
}
.back {
  padding: 7px;
  color: #fff !important;
  font-size: 25px !important;
}
.delete {
  color: red !important;
  font-size: 15px;
}
.permissionMain {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.checkButton {
  width: 100%;
  height: 100%;
  margin-left: 40%;
  margin-top: 4%;
}
.manageBtn {
  background-color: #2295fb;
  color: white;
}
</style>



