<template>
<!-- 角色管理组件 -->
  <div class="permissionRoleTable">
    <div class="createButton">
      <el-button type="primary" @click.stop="dialogFormVisible = true">创建新角色</el-button>
    </div>
    <!-- 角色信息表格，与新建用户表格共用位置 -->
    <div class="table">
      <el-table :data="tableData" stripe style="width: 100%" height="380px" >
        <el-table-column prop="ROLENAME" label="角色名称" width="200px" >
          <template slot-scope="scope">
           <span style="margin-left: 10px">{{ scope.row.ROLENAME }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="ROLEMEMO" label="角色说明" width="280px" >
          <template slot-scope="scope">
           <span style="margin-left: 10px">{{ scope.row.ROLEMEMO }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="activity" label="操作" width="520px">
          <template slot-scope="scope">
            <el-button class="changeBtn" type="primary" @click.stop="classClick(scope.row)">编辑门类</el-button>
            <el-button class="changeBtn" type="primary" @click.stop="roleClick(scope.row)" >编辑权限</el-button>
            <el-button class="changeBtn" type="primary"  @click.stop="bindClick(scope.row)">编辑绑定用户</el-button>
            <el-button class="changeBtn" type="primary" @click.stop="changeClick(scope.row)">编辑角色</el-button>
            <span>
              <i class="iconfont icon-shanchu delete" @click.stop="handleDelete(scope.row)"></i>
            </span>
          </template>
        </el-table-column>
      </el-table>
      <!-- <div class="permissionBind">
      <PermissionBind v-if="dialogFormVisible1" :dialogFormVisible1="true">
        <div slot="Bind"></div>
      </PermissionBind>
      </div> -->
      <!-- <div class="permissionAdd">
      <PermissionAdd :visible.sync="visble"></PermissionAdd>
      </div> -->
      <div class="pageCtl" :is="pagination"></div>
    </div>
    <!-- 新建角色的表单 -->
    <div class="createForm">
      <el-dialog title="新建角色" :visible.sync="dialogFormVisible" :modal="false" hide-required-asterisk = "true">
        <div class="createFormMain">
        <el-form :model="form" :inline="true" ref="form">
          <el-form-item label="角色名称" prop="ROLENAME" :label-width="formLabelWidth" @blur="checkNameExist" status-icon="true">
            <el-input type="text"  v-model="form.ROLENAME" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="角色说明" prop="ROLEMEMO" :label-width="formLabelWidth">
            <el-input  type="text" v-model="form.ROLEMEMO" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        </div>
        <div slot="footer" class="roleForm-footer">
        <el-button @click="btnResetClickForm('form')">重 置</el-button>
        <el-button type="primary" @click="submitRoleData('form')">保 存</el-button>
        </div>
      </el-dialog>
    </div>
    <!-- 修改信息提交表单 -->
    <div class="fixForm">
      <el-dialog title="修改角色--"
      :visible.sync = "dialogFormVisible3"
      :modal="false"
      :paramsRoolId = "paramsRoolId"
      :paramsRoolRole = "paramsRoolRole">
         <div class="showName">
           <slot>{{paramsRoolRole}}</slot>
         </div>
        <div class="createFormMain">
        <el-form :model="form1" :inline="true" ref="form1">
            <el-input type="hidden" prop="ROLEID" v-model="form1.ROLEID" autocomplete="off"></el-input>
          <el-form-item label="角色名称" prop="ROLENAME" :label-width="formLabelWidth">
            <el-input type="text"  v-model="form1.ROLENAME" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="角色说明" prop="ROLEMEMO" :label-width="formLabelWidth">
            <el-input  type="text" v-model="form1.ROLEMEMO" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        </div>
        <div slot="footer" class="roleForm-footer">
        <el-button @click="btnResetClickForm1('form1')">重 置</el-button>
        <el-button type="primary" @click="editClick('form1')">保 存</el-button>
        </div>
      </el-dialog>
    </div>
    <!-- 绑定用户框 -->
    <div class="choseForm">
      <el-dialog title="绑定用户--" :visible.sync="dialogFormVisible1" :modal="false" :paramsRoolBind = "paramsRoolBind">
        <div class="showName">
           <slot>{{paramsRoolBind}}</slot>
         </div>
        <div class="choseFormMain">
          <el-checkbox-group class="checkboxgroup" v-model="checkedRoles" @change="handleCheckedChange" >
            <el-checkbox v-for="role in roles" :label="role" :key="role" border>{{role}}</el-checkbox>
          </el-checkbox-group>
        </div>
        <div slot="footer" class="choseForm-footer">
          <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
          <el-button >重 置</el-button>
          <el-button type="primary">保 存</el-button>
        </div>
      </el-dialog>
    </div>
     <!-- 新增权限表单 -->
    <div class="createPermission">
      <el-dialog title="新增权限--" :visible.sync="dialogFormVisible2" :modal="false" :paramsRoolAdd = "paramsRoolAdd">
        <div class="showName">
           <slot>{{paramsRoolAdd}}</slot>
         </div>
        <div class="chooseTab">
          <el-tabs
           v-model="activeName"
           type="card"
           @tab-click="getMenu"
           :paramsId="paramsId"
           >
          <el-tab-pane
            v-for="(item, index) in treeArray"
           :label="item.label"
           :key="index"
           :name="item.name"
           >
          <div class="permissionPane" >
            <el-tree
              :data="collapseList"
              show-checkbox
              node-key="id"
              :props="defaultProps"
              highlight-current
              @check-change="handleCheckChange"
              :default-checked-keys="defaultCheck">
            </el-tree>
           </div>
           </el-tab-pane>
          </el-tabs>
          </div>
          <div slot="footer" class="createPermission-footer">
          <el-button >重 置</el-button>
          <el-button type="primary" @click.stop="getCheckedKeys">保 存</el-button>
        </div>
      </el-dialog>
    </div>
   <!-- 新增门类 -->
     <div class="createroleClass">
      <el-dialog title="新增门类  -" :visible.sync="dialogFormVisible4" :modal="false" :paramsRoolClass = "paramsRoolClass">
        <div class="showName">
           <slot>{{paramsRoolClass}}</slot>
         </div>
        <div class="addTab">
          <el-tabs
          type="card"
           v-model="activeName"
           @tab-click="getClass"
           >
          <el-tab-pane v-for="(item, index) in securityArray"
           :label="item.label"
           :key="index"
           :name="item.name">
           </el-tab-pane>
          </el-tabs>
        </div>
        <div class="addTree">
          <el-tree
              :data="collapseList"
              show-checkbox
              node-key="id"
              :props="defaultProps"
              default-expand-all="true"
              @check-change="handleCheckChange"
             >
            </el-tree>
        </div>
        <div class="addBox">
          <div class="addTitle">
            <p>01:/请选择密级级别:<el-checkbox class="check"
            @change="handleCheckAllSecurityChange"
            :indeterminate="isIndeterminate"
            >全选</el-checkbox></p>
          </div>
          <div class="showCheckbox">
            <el-checkbox-group  v-model="checkedSecurity" @change="handleCheckedSecurity">
              <el-checkbox v-for="security in securities" :label="security" :key="security" border>{{security}}</el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
          <div slot="footer" class="createClass-footer">
          <el-button type="primary" @click="dialogFormVisible4 = false">保 存</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import pagination from './Pagination'
// import permissionAdd from './PermissionAdd'
// import permissionBind from './PermissionBind'
const roleOptions = ['客户', '经理', '客户', '经理']
const securityOptions = ['秘密', '公开', '国内', '全部']
export default {
  name: 'permissionRoleTable',
  data () {
    return {
      // 表单
      form: {
        ROLENAME: '', // 角色名称
        ROLEMEMO: '' // 角色说明
      },
      form1: {
        ROLEID: '', // 角色id
        ROLENAME: '', // 角色名称
        ROLEMEMO: '' // 角色说明
      },
      checkBoxData: [],
      pageIndex: 1,
      pageSize: 10,
      tableData: [],
      total: '',
      formLabelWidth: '70px',
      pagination: 'pagination',
      isShow: true,
      // 弹框
      dialogFormVisible: false,
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      dialogFormVisible3: false,
      dialogFormVisible4: false,
      // 绑定框
      isIndeterminate: true,
      checkAll: false,
      checkedRoles: [],
      roles: roleOptions,
      // 新增树
      activeName: '',
      treeArray: [
        {
          label: '数据接收',
          name: '0'
        },
        {
          label: '辅助归档',
          name: '1'
        },
        {
          label: '档案管理',
          name: '2'
        },
        {
          label: '借阅管理',
          name: '3'
        },
        {
          label: '系统管理',
          name: '4'
        },
        {
          label: '远程数据移交',
          name: '5'
        }
      ],
      tabIndex: 6,
      paramsRoolId: '',
      paramsId: '',
      paramsRoolRole: '',
      paramsRoolBind: '',
      paramsRoolClass: '',
      paramsRoolAdd: '',
      collapseList: [],
      roleCheckedList: [],
      roleList: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      leafOnly: [],
      // 默认显示的数组
      defaultCheck: [],
      // 新增门类涉及的数据
      checkedSecurity: [],
      securities: securityOptions,
      checkAllSecurity: false,
      securityArray: [
        {
          label: '门类1',
          name: '0'
        }
      ]
    }
  },
  components: {
    pagination
    // permissionAdd,
    // permissionBind
  },
  mounted () {
    this.getRoles()
  },
  methods: {
    // 修改角色获取行数按钮
    changeClick (row) {
      this.dialogFormVisible3 = true
      let id = row.ROLEID
      let roleShow = row.ROLENAME
      this.paramsRoolId = id
      this.paramsRoolRole = roleShow
    },
    // 修改角色表单提交
    editClick: function (form1) {
      let that = this
      that.$axios.put(that.api + '/RoleManagerServlet' + that.actionExt, {
        data: {
          ROLEID: that.paramsRoolId,
          ROLENAME: that.form1.ROLENAME,
          ROLEMEMO: that.form1.ROLEMEMO
        }
      }).then(function (response) {
        console.log(response)
        if (response.data.code === 0) {
          that.$message('修改角色成功！')
        } else {
          that.$message.error(response.data.errMsg)
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    // 获取绑定用户信息的列
    bindClick: function (row) {
      this.dialogFormVisible1 = true
      let roleShow = row.ROLENAME
      this.paramsRoolBind = roleShow
    },
    // 获取门类的列
    classClick: function (row) {
      this.dialogFormVisible4 = true
      let roleShow = row.ROLENAME
      this.paramsRoolClass = roleShow
    },
    // 新建角色
    submitRoleData: function (form) {
      let that = this
      that.$axios.post(that.api + '/RoleManagerServlet' + that.actionExt, {
        data: {
          ROLENAME: that.form.ROLENAME,
          ROLEMEMO: that.form.ROLEMEMO
        }
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message('新建角色成功！')
        } else {
          that.$message.error(response.data.errMsg)
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    // 用户名重复检查
    checkNameExist: function () {
      let that = this
      that.$axios.post(that.api + '/RoleManagerServlet' + that.actionExt, {
        data: {
          ROLENAME: that.form.ROLENAME
        }
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message('您输入了重复的用户名！')
        } else {
          that.$message.error(response.data.errMsg)
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    // 查看角色
    getRoles: function () {
      let that = this
      that.$axios.get(that.api + '/RoleManagerServlet' + that.actionExt, {
        params: {
          pageIndex: that.pageIndex,
          pageSize: that.pageSize
        }
      }).then(function (response) {
        console.log(response)
        if (response.data.code === 0) {
          that.tableData = response.data.list
          that.total = response.data.count
        } else {
          that.$message.error(response.data.errMsg)
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    // 单个删除角色
    handleDelete (row) {
      let that = this
      console.log('111', row.ROLEID)
      this.$confirm('您确定删除吗?删除后无法恢复！', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        console.log('1111111')
        that.$axios.delete(that.api + '/RoleManagerServlet' + that.actionExt, {
          data: {
            ROLEID: row.ROLEID
          }
        }).then(function (response) {
          console.log('跳入验证', response)
          if (response.data.code === 0) {
            that.$message('成功删除角色！')
          } else {
            that.$message.error(response.data.errMsg)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        })
      })
    },
    // 重置方法
    btnResetClickForm (form) {
      this.$nextTick(() => {
        this.$refs['form'].resetFields()
      })
    },
    btnResetClickForm1 (form1) {
      this.$nextTick(() => {
        this.$refs['form1'].resetFields()
      })
    },
    // 新增用户的全选的方法
    handleCheckAllChange (val) {
      this.checkedRoles = val ? roleOptions : []
      this.isIndeterminate = false
    },
    handleCheckedChange (value) {
      let checkedCount = value.length
      this.checkAll = checkedCount === this.roles.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.roles.length
    },
    // 新增密级的全选方法
    handleCheckedSecurity (value) {
      let checkedCount = value.length
      this.checkAllSecurity = checkedCount === this.securities.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.securities.length
    },
    handleCheckAllSecurityChange (val) {
      this.checkedSecurity = val ? securityOptions : []
      this.isIndeterminate = false
    },
    // 获取角色功能菜单列表和选中列表,可以分别展示在树上
    roleClick (row) {
      this.dialogFormVisible2 = true
      let that = this
      let roleShow = row.ROLENAME
      that.paramsRoolAdd = roleShow
      let typeId = row.ROLEID
      that.$axios.get(that.api + '/RoleFun' + that.actionExt, {
        params: {
          menuType: 0
        }
      }).then(function (response) {
        console.log(response)
        if (response.data.code === 0) {
          that.collapseList = response.data.roleMenu
        } else {
          that.$message.error(response.data.errMsg)
        }
        that.$axios.post(that.api + '/RoleFun' + that.actionExt, {
          params: {
            menuType: typeId
          }
        }).then(function (response) {
          console.log(111111)
          console.log(response)
          if (response.data.code === 0) {
            that.defaultCheck = response.data.roleRight
          } else {
            that.$message.error(response.data.errMsg)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }).catch(function (error) {
        console.log(error)
      })
    },
    // 分别得到该标题的展示信息
    getMenu: function () {
      let that = this
      let typeId = that.activeName
      that.$axios.get(that.api + '/RoleFun' + that.actionExt, {
        params: {
          menuType: typeId
        }
      }).then(function (response) {
        console.log(111111)
        console.log(response)
        if (response.data.code === 0) {
          that.collapseList = response.data.roleMenu
        } else {
          that.$message.error(response.data.errMsg)
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    // 显示树
    handleCheckChange (data, checked, indeterminate) {
      console.log(data, checked, indeterminate)
    },
    handleNodeClick (data) {
      console.log(data)
    },
    // 上传选中的所有角色权限信息
    getCheckedKeys: function (leafOnly) {
      console.log(leafOnly)
      let that = this
      that.$axios.post(that.api + '/RoleFunAssgin' + that.actionExt, {
        roleId: '',
        datas: {
          ROLEID: that.paramsId,
          MENUID: that.leafOnly.id
        }
      }).then(function (response) {
        console.log(response)
        if (response.data.code === 0) {
          that.$message('已成功分配该角色的权限！')
        } else {
          that.$message.error(response.data.errMsg)
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    // 给用户显示已经选过的复选框
    // getdefaultCheck: function() {
    //   let that = this
    //   that.$axios.post(that.api + '/RoleFun' + that.actionExt, {
    //     params: {
    //       menuType: typeId
    //     }
    //   }).then(function (response) {
    //     console.log(111111)
    //     console.log(response)
    //     if (response.data.code === 0) {
    //       that.defaultCheck = response.data.roleRight
    //     } else {
    //       that.$message.error(response.data.errMsg)
    //     }
    //   }).catch(function (error) {
    //     console.log(error)
    //   })
    // }
    getClass: function () {
      console.log('获取门类')
    }
  }
}
</script>
<style>
.delete {
  color: #ed5b23 !important;
  margin-left: 2%;
}
.table {
  width: 100%;
  height: 100%;
  margin-left: 16%;
}
.pageCtl{
  margin-left:35%;
  margin-top:2%
}
.createButton {
  width: 100%;
  height: 100%;
  margin-left: 80%;
  margin-top: 2%;
}
.choseFormMain{
  margin-top: 6%;
  width: 600px;
  overflow: auto;
  height: 300px;
  margin-left: 4%;
}
.el-checkbox.is-bordered{
  width: 105px;
}
.el-checkbox{
  margin-top: 5px;
  margin-left: 10px;
}
.choseForm-footer,.createPermission-footer {
  margin-right: 40%;
}
.changeBtn:focus {
  background-color: #f69944;
  border-color: #f69944;
  color: #fff;
}
.createFormMain {
  margin-left:4%;
  margin-top: 1%;
}
.permissionPane{
  max-height: 200px;
  overflow: auto;
  margin-top: 4%;
  height: 250px;
  margin-left: 50px;
}
.roleForm-footer{
  margin-right:40% ;
}
.el-tabs__nav-scroll {
  max-width: 500px;
  overflow: auto;
  overflow-x: hidden;
}
.el-tabs__item{
  border-left: none;
}
.el-tabs__nav{
  border: none;
}
.el-tabs__nav-wrap.is-scrollable.{
  padding: 0 60px;
}
.chooseTab,.addTab{
  margin-top: 8%;
}
.showName{
  margin-top: -10%;
  margin-left: 14%;
  font-size: 14px;
  color: #727272;
  font-weight:bold;
}
.el-dialog__title{
  color: #2295fb;
  font-size: 16px;
  font-weight:bold;
}
.addTree{
  width: 40%;
  height: 350px;
  max-height: 350px;
  overflow: auto;
}
.addBox {
  height: 350px;
  width: 50%;
  max-height: 300px;
  overflow: auto;
  margin-top: -45%;
  margin-left: 50%;
}
.check {
 height: 10px;
 width: 10px;
 margin-left: 35%;
}
.addTitle {
  margin-top: -5%;
}
.createClass-footer{
  margin-right: 45%;
}
/* .permissionAdd{
  width: 20%;
  height: 30%;
  color: #ed5b23;
  z-index: 500;
  margin-top: -20%;
} */
</style>



